import * as THREE from 'three';
import * as dat from 'dat.gui';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js' 


const gui = new dat.GUI()
const world = {
    plane: {
        width: 10,
        height: 10,
        widthSegments: 10,
        heightSegments: 10
    }
}

function generatePlane() {
    planeMesh.geometry.dispose()
    planeMesh.geometry = new THREE.PlaneGeometry(
        world.plane.width,
        world.plane.height,
        world.plane.widthSegments,
        world.plane.heightSegments);

    const {array} = planeMesh.geometry.attributes.position;
    for (let i = 0; i < array.length; i += 3) {
        const x = array[i];
        const y = array[i + 1];
        const z = array[i + 2];

        array[i + 2] = z + Math.random()
    }
}

gui.add(world.plane, 'width', 1, 20)
    .onChange(generatePlane)

gui.add(world.plane, 'height', 1, 20)
    .onChange(generatePlane)

gui.add(world.plane, 'widthSegments', 1, 20)
    .onChange(generatePlane)

gui.add(world.plane, 'heightSegments', 1, 20)
    .onChange(generatePlane)

const raycaster = new THREE.Raycaster()
const scene = new THREE.Scene();
const camera = new THREE.PerspectiveCamera(
    75, 
    innerWidth / innerHeight, 
    0.1, 
    1000);

const renderer = new THREE.WebGLRenderer()
renderer.setSize(innerWidth, innerHeight);
renderer.setPixelRatio(devicePixelRatio);

document.body.appendChild(renderer.domElement);


const controls = new OrbitControls( camera, renderer.domElement );


camera.position.z = 5

const planeGeometry = new THREE.PlaneGeometry(5, 5, 10, 10);
const planeMaterial = new THREE.MeshPhongMaterial({
    color: 0xff0000,
    side: THREE.DoubleSide,
    flatShading: THREE.FlatShading
})
const planeMesh = new THREE.Mesh(
    planeGeometry, planeMaterial)
scene.add(planeMesh)

const {array} = planeMesh.geometry.attributes.position;


for (let i = 0; i < array.length; i += 3) {
    const x = array[i];
    const y = array[i + 1];
    const z = array[i + 2];

    array[i + 2] = z + Math.random()
}

planeMesh.geometry.setAttribute('color',
    new THREE.BufferAttribute(new Float32Array([0, 0, 1]), 3))


const light = new THREE.DirectionalLight(0xffffff, 1)
light.position.set(0, 0, 1)
scene.add(light)

const backLight = new THREE.DirectionalLight(0xffffff, 1)
backLight.position.set(0, 0, -1)
scene.add(backLight)


const mouse = {
    x: undefined,
    y: undefined
}

function animate() {
    requestAnimationFrame(animate)
    controls.update();

    renderer.render(scene, camera)

    raycaster.setFromCamera(mouse, camera)
    const intersects = raycaster.intersectObject(planeMesh)




    /*    planeMesh.rotation.x += 0.01*/
}

animate()



addEventListener('mousemove', (e) => {
    mouse.x = (e.clientX / innerWidth) * 2 - 1
    mouse.y = -(e.clientY / innerHeight) * 2 + 1
})
